





































































@import '../../styles/variables.scss';

.modal__inner-1.modal__inner-1--badge-management {
  max-width: 520px;

  p {
    font-size: 1.8rem;
    font-weight: bold;
  }
}

.badge-entry {
  line-height: 1.4;
  margin: 2rem 0;
  font-size: 0.9em;

  .badge-entry {
    font-size: 0.8em;
    margin: 0.75rem 0;
  }
}

.badge-entry__icon {
  width: 1.67em;
  height: 1.67em;
  margin-right: 0.25em;
  opacity: 0.3;
  flex: none;
}

.badge-entry__icon--some-earned {
  opacity: 0.5;
  color: goldenrod;
}

.badge-entry__icon--earned {
  opacity: 1;
  color: goldenrod;
}

.badge-entry__description {
  opacity: 0.6;
}

.badge-entry__name {
  font-size: 1.2em;
  font-weight: bold;
  opacity: 0.4;
}

.badge-entry__name--earned {
  opacity: 1;
}



